.search-result-container {
	display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 10px;
}

.search-result {
	width: 100%;
}

.search-result h1:nth-child(1) {
	font-size: 20px;
	padding-left: 0;
}

.search-title-container {
	display: flex;
	justify-content: space-between;
}